import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { ModalController, ActionSheetController, IonSlides, AnimationController } from '@ionic/angular';
import { EstadoCitasService } from 'src/app/providers/Api/EstadoCitas.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import * as moment from 'moment';
import { trigger, transition, animate, style } from '@angular/animations';
moment.locale('es');

@Component({
  selector: 'app-modal-citas',
  templateUrl: './modal-citas.page.html',
  styleUrls: ['./modal-citas.page.scss'],
  animations: [
    trigger('entrada', [
      transition(':enter', [
        style({
          opacity: 0,
          transform: 'scale(0)'
        }),
        animate('250ms', style({
          opacity: 1,
          transform: 'scale(1)'
        }))
      ]),

      transition(':leave', [
        style({
          opacity: 1,
          transform: 'scale(1)'
        }),
        animate('250ms', style({
          opacity: 0,
          transform: 'scale(0)'
        }))
      ])
    ])
  ]
})
export class ModalCitasPage implements OnInit {

  
  @Input() type: any;
  @Input() data: any;
  

  titulo = 'Configuración';
  vista;
  @ViewChild('slide', {static: false}) slide: IonSlides;
  listaTalleres = [];
  mensaje = '';
  estado = '';
  estadoCambio = '';
  role = '';

  
  usuario  = '';
  taller: any;

  urlArchivo: any = '';
  tipoArchivo: any = '';

  slideOpt = {
    zoom: {
      maxRatio: 3,
      minRatio: 1,
      toggle: true
    }
  };

  citas = [];
  texto = '';


  // Sistema de Trabajos en el local

  listaCreados = [];
  trabajos = [];
  nuevoTrabajo = false;
  textoTrabajo = '';
  textoBuscar = '';

  precioconfig = false;
  precio = 0;
  msgEstado = '';
  msgpro = '';
  msgter = '';
  terminado = false;

  constructor(
    private modalCtrl: ModalController,
    private actionSheetCtrl: ActionSheetController,
    private citasService: EstadoCitasService,
    private utitlities: ToastServices,
    private almacenamiento: InicioService,
    private animationCtrl: AnimationController,
    private toast: ToastServices
  ) { 
    
  }

  ngOnInit() {
  }

   async ionViewWillEnter() {
    
    switch (this.type) {
      case 'setting':
        const ui = await this.almacenamiento.obtenerToken();

        if (ui) {
          this.citasService.obtenerListaTrabajos(ui.token).subscribe((res: any) => {
            console.log(res, 'Respuesta');
            if (res.exe) {
              this.listaCreados = res.response[0].listaTrabajos;
            }
            
          })
          console.log(1)
        }
        console.log(2)
        this.vista = 'setting';
        break;

      case 'buscar':
        this.titulo = 'Buscar Talleres';
        this.vista = 'buscar';
        setTimeout(() => {
          this.slide.lockSwipes(true).then(() => {
            console.log('OK')
          })
        }, 500);

        this.listarTalleres();

        break;
          case 'media':
            this.vista = 'media';
            this.urlArchivo = this.data.url;
            this.tipoArchivo = this.data.tipo;
            break;

            case 'carpetas':
            this.vista = 'carpetas';
            console.log(this.data, 'BUENA ESA VALLE')
            if (this.data.role === 'TALLER_ROLE') {
              const filtrado = this.data.info.filter((item) => item.usuariopedido._id === this.data.unico)
              this.citas = filtrado;
            } else {
              const filtrado = this.data.info.filter((item) => item.idtaller._id === this.data.unico)
              this.citas = filtrado;
            }
            
            break;

      default:
        break;

    }
  }

  filtro ( event ) {
    this.texto = event.detail.value;
  }

  



  async guardar() {
    
      const usuario = await this.almacenamiento.obtenerToken();
  
      if (usuario) {
            this.modalCtrl.dismiss({
              estado: this.estadoCambio,
              info: {
                trabajos: this.trabajos,
                precio: this.precio
              }
            });
            this.nuevoTrabajo = false;
      }
  }

  escogerChat(cita) {
    this.modalCtrl.dismiss({
      salir: true,
      cita
    })
  }

  descartar() {
    this.modalCtrl.dismiss({
      salir: true
    })
  }

 
  listarTalleres() {
    this.almacenamiento.obtenerToken().then((info: any) => {
      this.citasService.listadoTalleres(info.token).subscribe((data: any) => {

        if (data.response.length > 0) {
            data.response.forEach((item) => {
              item.avatar = item.usuarioReceptor.imagen[0].path;
              this.listaTalleres.push(item);
            });
        }
        console.log(this.listaTalleres, data);
      })
    })
  }

  async opciones(usuario) {
    await this.slide.lockSwipes(true);
    const actionSheet = await this.actionSheetCtrl.create({
      translucent: true,
      header: `Que quieres hacer con ${usuario}`,
      buttons: [
        {
          text: 'Compartir Perfíl',
          icon: 'arrow-redo-outline',
          handler: () => {

          }
        },
        {
          text: 'Enviar mensaje',
          icon: 'chatbubble-outline',
          handler: () => {

          }
        },
        {
          text: 'Invitar a grupo',
          icon: 'people-outline',
          handler: () => {

          }
        },
        {
          text: 'Eliminar Usuario',
          icon: 'trash-outline',
          cssClass: 'eliminar',
          handler: () => {

          }
        },
        {
          text: 'Cancelar',
          role: 'cancel',
          icon: 'close-outline'
        }
      ]
    })

    return await actionSheet.present();
  }

  async crear( item ) {
    await this.slide.lockSwipes(false);
    await this.slide.slideNext(300);
    this.usuario = item.usuarioReceptor.nombrecompleto;
    this.taller = item;
    await this.slide.lockSwipes(true);
  }

  generarTicket() {
    console.log('Taller', this.taller.usuarioReceptor._id);
    this.almacenamiento.obtenerToken().then((info: any) => {
      this.citasService.crearCita(info.token, {idtaller: this.taller.usuarioReceptor._id}).subscribe(async (data) => {

        console.log(data);
        await this.modalCtrl.dismiss({});
  
      }, async (error) => {
        await this.utitlities.alertApplication('No se pudo agendar la cita', false);
        await this.modalCtrl.dismiss({});
      });
    })
  }

  async cancelar() {
    await this.slide.lockSwipes(false);
    await this.slide.slidePrev(300);
    await this.slide.lockSwipes(true);
  }


  // CONFIGURACIONES


  filtroTrabajo( event ) {
    this.textoBuscar = event.detail.value;
  }



  check( trabajo ): boolean {
    return this.trabajos.find((item) => item === trabajo)
  }

  borrarTrabajo ( i: number ) {
    this.trabajos.splice(i, 1);
  }

  habilitarTrabajo() {
      this.nuevoTrabajo = true;
  }
}